.component-select-with-image {
  border: var(--bs-border-width) solid var(--bs-border-color);
  box-shadow: var(--ui-shadow);
  display: flex;
  flex-direction: column;
  width: 100%;
  border-radius: var(--bs-border-radius);
  overflow: hidden;
  padding: 0.35rem 0.5rem;
}
.component-select-with-image .select-with-image-item {
  height: 3rem;
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 0.2rem;
  overflow: hidden;
  background-position-x: 0.5rem;
}
.component-select-with-image .select-with-image-item.selected {
  background-color: var(--bs-primary);
}

.zero-size-invisible {
  width: 0;
  height: 0;
  visibility: hidden;
  pointer-events: none;
  overflow: hidden;
}

[data-bs-theme=dark] .component-select-with-image .select-with-image-item {
  filter: invert(1);
}
[data-bs-theme=dark] .component-select-with-image .select-with-image-item.selected {
  filter: none;
}